import React, { useContext } from 'react';
import userName from './App'
export default function Header() {
const userName1 = useContext(userName)
return (
    <div className="header">
        <p>Hello {userName1}</p>
        <ul>
        <li>Home</li>
        <li>Contact</li>
        <li>About</li>
        </ul>
       
    </div>
)
}